import { NextResponse } from 'next/server';
import { PrismaClient } from '@prisma/client';

const prisma = new PrismaClient();

// 缓存数据和过期时间
let cachedStats: any = null;
let cacheExpiry: number = 0;
const CACHE_DURATION = 60 * 1000; // 1分钟缓存

// 获取仪表盘统计数据
export async function GET() {
  try {
    // 检查缓存是否有效
    const now = Date.now();
    if (cachedStats && now < cacheExpiry) {
      return NextResponse.json(cachedStats, { status: 200 });
    }

    // 从数据库获取实际数据
    const [totalPosts, totalUsers, totalComments, totalCategories] = await Promise.all([
      prisma.post.count(),
      prisma.user.count(),
      prisma.comment.count(),
      prisma.category.count()
    ]);

    // 构建响应数据
    const stats = {
      totalPosts,
      totalUsers,
      totalComments,
      totalCategories,
      lastUpdated: new Date().toISOString()
    };

    // 更新缓存
    cachedStats = stats;
    cacheExpiry = now + CACHE_DURATION;

    return NextResponse.json(stats, { status: 200 });
  } catch (error) {
    console.error('获取仪表盘统计数据失败:', error);
    return NextResponse.json({ error: '获取数据失败，请稍后重试' }, { status: 500 });
  } finally {
    await prisma.$disconnect();
  }
}